
.logo-container{
  width: 100%;
  height: 100%;
}
.logo-from{
  position: absolute;
  width: 400px;
  height: 400px;
  left: 50%;
  top: 50%;
  margin-left: -200px;
  margin-top:-200px;
}
.logo-from h1,.logo-from-sm h1{
	font-size: 20px;
	color: #fff;
	font-family: 'Arial';
	font-weight: normal;
}
.ity-input{
	border:0 none;
	border-bottom: 1px dotted #fff;
	background-color: transparent !important;
	width: 100%;
	height: 50px;
	color: #fff;
	padding-left: 15px;
}
.ity-btn{
	width:80px;
	height: 40px;
	background-color: #fff;
	border: 0 none;
	margin-top: 10px;
	float: right;
	color:#5D92BA;
}
.logo-from-sm{
	visibility: hidden;
}
@media screen and (max-width:768px){
  /*admin*/
.logo-from-sm{
    padding: 20px;
    position: absolute;
    width: 100%;
    height: 220px;
    top: 50%;
    margin-top:-110px;
    visibility: visible !important;
}
.logo-from{
    display: none;
}
}





